import React, { useEffect, useMemo, useState } from 'react'
import { Badge, Button, Table } from 'antd';
import { FillQuestionHub } from '../../../../hub/FillQuestionHub/FillQuestionHub';
import { Link, useHistory } from 'react-router-dom';
import { PlusOutlined } from '@ant-design/icons';
import "./list-check-parts.less"
import { ListVehiclesResult, VehiclesSituationType } from '../../models/ListVehicles';
import { VehicleApi } from '../../api/VehicleApi';
import SituationVehicle from './components/situation-vehicle/SituationVehicle';
import { FillQuestionMessage } from '../../../../hub/FillQuestionHub/models/FillQuestionMessage';
import { EventService } from '../../../../services/Utils/event/event.service';
import { EventType } from '../../../../services/Utils/event/event.types';


const ListCheckParts: React.FC = () => {

    const { push } = useHistory();
    const [loading, setLoading] = useState(false);
    const [data, setData] = useState<ListVehiclesResult[]>()
    const colunas = useMemo(() => [
        {
            title: 'Placa',
            dataIndex: 'licensePlate',
            key: 'licensePlate',
        },
        {
            title: 'Modelo',
            dataIndex: 'model',
            key: 'model',
        },
        {
            title: 'Marca',
            dataIndex: 'brand',
            key: 'brand',
        },
        {
            title: 'Cor',
            dataIndex: 'color',
            key: 'color',
        },
        {
            title: 'Ano',
            dataIndex: 'year',
            key: 'year',
        },
        {
            title: 'Situao veculo',
            key: 'situation',
            dataIndex: "situation",
            width: "130px",
            render: (situation: VehiclesSituationType) => <SituationVehicle situation={situation} />
        },
        {
            title: 'ltima ficha',
            key: 'brand',
            dataIndex: "lastFillQuestion",
            render: (date?: Date) => date ?? "-"
        },
    ], []);

    useEffect(() => {
        onListVehicle();

        const key = FillQuestionHub.onReceiveMessage((message) => {
            handlerNewFillQuestion(message);
        });

        const cancelation = EventService.listen(EventType.new_vehicle, () => onListVehicle());

        return () => {
            FillQuestionHub.unRegister(key);
            cancelation();
        }
    }, []);

    function handlerNewFillQuestion(message: FillQuestionMessage) {
        setData(old => old?.map(e => {
            if (e.id === message.identifierVehicle) {
                return { ...e, ...message, id: e.id, update: true }
            }
            return e
        }))
    }

    async function onListVehicle() {
        setLoading(true)
        try {

            let result = await VehicleApi.listAll();
            setData(result.data)
        }
        finally {
            setLoading(false)
        }
    }

    const DraggableBodyRow = (props: any) => {

        if (props?.record?.update) {
            return <tr {...props} style={{ background: "#FAD24A" }} />;
        }

        return <tr {...props} />;
    };

    return (
        <>
            <div className="actions-box">
                <div></div>

                <Link to="/check-parts/new">
                    <Button type="primary" icon={<PlusOutlined />} size="large">
                        NOVO
                    </Button>
                </Link>
            </div>

            <Table rowKey="id" columns={colunas} dataSource={data} size="middle"
                components={{
                    body: {
                        row: DraggableBodyRow,
                    }
                }}
                loading={loading}
                onRow={(record) => {
                    return {
                        onClick: () => {
                            push(`/check-parts/history/${record.id}`);
                            setData(old => old?.map(e => {
                                if (e.id === record.id) {
                                    return { ...e, update: undefined }
                                }
                                return e
                            }))
                        },
                        record: record
                    }
                }} />
        </>
    )
}

export default ListCheckParts
