import React, { useEffect, useState } from 'react'
import { Col, Row } from 'antd'
import { CreatePoliceReportCommand } from '../../models/CreatePoliceReport'
import { Input, Switch, TextArea } from '../../../../components/form/fields/WithFormItem'
import { PoliceReportProps } from './PoliceReport.types'
import { useForm } from 'react-hook-form'
import CreatePoliceReportSchema from './CreatePoliceReportSchema'
import Form from '../../../../components/form/form/Form'
import Modal from 'antd/lib/modal/Modal'

import "./PoliceReport.less"
import { PositionReportApi } from '../../api/PositionReportApi'
import { message } from 'antd';

const PoliceReport: React.FC<PoliceReportProps> = (props) => {

    const defaultValue: CreatePoliceReportCommand = { latitude: 1, longitude: 1, isAnonymus: false, description: null };

    const [loading, setLoading] = useState<boolean>(false);
    const form = useForm<CreatePoliceReportCommand>({ resolver: CreatePoliceReportSchema, defaultValues: defaultValue, });

    useEffect(() => {
        form.reset({
            ...defaultValue,
            latitude: props.latitude,
            longitude: props.longitude,
        })
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [props.latitude, props.longitude])


    const onSubmit = async (data: CreatePoliceReportCommand) => {
        setLoading(true);

        try {
            await PositionReportApi.Save(data);
            message.success("Ocorrncia cadastrada com sucesso!");
            props.onSuccess();
        }
        catch (e) {
            message.error("Erro crtico.")
        }
        finally{
            setLoading(false)
        }
    };

    return (
        <Modal title="Cadastro de Ocorrncia"
            visible={props.isModalVisible}
            okText="Salvar"
            onOk={form.handleSubmit(onSubmit)}
            okButtonProps={{ loading: loading }}
            onCancel={() => props.onCancel()}>

            <Form loading={loading} provider={form}  >
                <Input label="latitude" name="latitude" hidden />
                <Input label="longitude" name="longitude" hidden />
                <Row>
                    <Col span={24}>
                        <TextArea label="Ocorrncia" name="description" rows={4} required />
                    </Col>
                </Row>

                <Row>
                    <Col span={4}>
                        <Switch label="Annimo?" name="isAnonymus" />
                    </Col>
                </Row>

                <Row>
                    <Col span={24}>
                        <Input label="Denunciante" name="whistleblower" required={!form.getValues().isAnonymus} />
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}

export default PoliceReport
