import React, { } from 'react'
import { Badge, Button, Col, Row } from 'antd'
import { Input, TextArea } from '../../../../../components/form/fields/WithFormItem'
import { ToolOutlined } from '@ant-design/icons';
import FormItem from '../../../../../components/form/form-item/FormItem'


const LastFillFormGeneral: React.FC = () => {


    return (
        <>
            <Row>
                <Col sm={24} md={8} lg={6}>
                    <Input label="Comandante Equipe" name="comandante" readOnly />
                </Col>
                <Col sm={24} md={8} lg={6}>
                    <Input label="Motorista" name="motorista" readOnly />
                </Col>
                <Col sm={24} md={8} lg={6}>
                    <FormItem label="Situao do veculo">
                        <Badge status="warning" text="Em uso" />
                    </FormItem>
                </Col>
            </Row>

            <Row>
                <Col sm={24} md={12} lg={6}>
                    <Input label="KM Inicial" name="startKm" readOnly />
                </Col>
                <Col sm={24} md={12} lg={6}>
                    <Input label="KM final" name="endkm" readOnly />
                </Col>
                <Col sm={12} md={8} lg={6}>
                    <Input label="Prxima troca de leo" name="a" readOnly />
                </Col>
                <Col sm={12} md={8} lg={6}>
                    <FormItem >
                        <Button
                            type="dashed"
                            icon={<ToolOutlined />}
                        >
                            Agendar troca de leo
                    </Button>
                    </FormItem>
                </Col>
            </Row>

            <Row>
                <Col sm={24} md={8} lg={6}>
                    <Input label="Horrio de inicio" name="startTime" readOnly />
                </Col>
                <Col sm={24} md={8} lg={6}>
                    <Input label="Horrio de fim" name="endTime" readOnly />
                </Col>
            </Row>

            <Row>
                <Col sm={24} md={24} lg={12}>
                    <TextArea label="Funcionamento Geral" rows={4} name="generalOperation" readOnly />
                </Col>
                <Col sm={24} md={24} lg={12}>
                    <TextArea label="Observaes" rows={4} name="comments" readOnly />
                </Col>
            </Row>
        </>
    )
}

export default LastFillFormGeneral
