import * as Yup from "yup";
import { yupResolver } from '@hookform/resolvers/yup';
import { CreateVehicleCommand } from "../../models/CreateVehicle";

const CreateVehicleSchema: Yup.SchemaOf<CreateVehicleCommand> = Yup.object().shape({
    brandId: Yup.string().nullable()
        .required("Marca  obrigatria"),

    chassi: Yup.string().nullable()
        .required("Chassi  obrigatrio.")
        .min(8, "Chassi invlido."),

    color: Yup.string().nullable()
        .required("Cor  obrigatrio.")
        .min(3, "Cor invlida."),

    description: Yup.string().nullable().optional(),

    details: Yup.string().nullable().optional(),

    licensePlate: Yup.string().nullable()
        .required("Placa  obrigatria.")
        .min(7, "Placa invlida."),

    model: Yup.string().nullable()
        .required("Modelo  obrigatrio.")
        .min(3, "Modelo invlido."),

    renavam: Yup.string().nullable()
        .required("Renavam  obrigatria."),

    situation: Yup.string().nullable().optional(),

    year: Yup.number().nullable()
        .required("Ano/Modelo  obrigatrio.")
        .min(1990, "Ano invlido.")
        .max(new Date().getFullYear(), "Ano invlido"),

})



export default yupResolver<CreateVehicleCommand>(CreateVehicleSchema);

