import React, { useEffect, useState } from 'react'
import { AllMarkersResult } from '../../models/AllMarkers';
import { Map2dApi } from '../../api/Map2dApi';
import { MapContainer, Marker, TileLayer, useMapEvents, } from 'react-leaflet';
import { message } from 'antd';
import VehicleMarkers from './components/VehicleMarkers/VehicleMarkers';

import 'leaflet/dist/leaflet.css';
import "./map-2d.less"
import SelectedPosition from './components/SelectedPosition/SelectedPosition';
import PoliceReportMarker from './components/PoliceReportMarker/PoliceReportMarker';


const Map2d: React.FC = () => {

    const [markers, setMarkers] = useState<AllMarkersResult>({ vehicles: [], policeReports: [] })



    useEffect(() => {
        init();

    }, []);

    async function init() {
        try {

            let result = await Map2dApi.getAllMarkers();

            if (result.data) {
                setMarkers(result.data!);
            }
        }
        catch {
            message.error("Erro ao carregar as informaes do mapa!")
        }
    }

    return (
        <div className="leaflet-container">
            <MapContainer
                zoom={13}
                style={{ height: "90vh" }}
                center={{ alt: 1000, lat: -25.531909, lng: -54.566674, }}
            >
                <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" zIndex={100} />

                <VehicleMarkers vehicles={markers.vehicles} />
                <PoliceReportMarker policeReports={markers.policeReports} />
                <SelectedPosition />
            </MapContainer>
        </div>
    )
}

export default Map2d
