import React from 'react'
import { Route } from 'react-router-dom';
import FormPoliceCar from './pages/form-vehicle/FormVehicle';
import HistoryCheckParts from './pages/history-check-parts/HistoryCheckParts';
import ListCheckParts from './pages/list-check-parts/ListCheckParts';

const Routes: React.FC = () => {

    return (
        <>
            <Route path="/check-parts" component={ListCheckParts} />
            <Route path="/check-parts/new" component={FormPoliceCar} />
            <Route path="/check-parts/history/:id" component={HistoryCheckParts} />
        </>
    )
}

export default Routes
