import React, { useEffect, useState } from 'react'
import { Layout, Menu } from 'antd';
import { Link } from 'react-router-dom';
import {
    VideoCameraOutlined,
    CarOutlined
} from '@ant-design/icons';

import policiaMilitarBrasao from "./../../images/policia-militar-brasao.png"
import "./basic-layout.less"
import BasicHeader from '../basic-header/BasicHeader';
import { NotificationHub } from '../../hub/Notifications/NotificationHub';
import { ReceiveNeedSOSVehicleMessage } from '../../hub/Notifications/models/ReceiveNeedSOSVehicleMessage';
import { ReceiveCancelSOSVehicleMessage } from '../../hub/Notifications/models/ReceiveCancelSOSVehicleMessage';

const { Header, Sider, Content } = Layout;

const BasicLayout: React.FC = (props) => {

    const [collapsed, setCollapsed] = useState(true);
    const [inSOS, setInSOS] = useState(false)

    useEffect(() => {
        let tokenNeedSOS = NotificationHub.onReceiveNeedSOS(onReceiveNeedSOS);
        let tokenCancelSOS = NotificationHub.onReceiveCancelSOS(onReceiveCancelSOS);

        return () => {
            NotificationHub.unRegister(tokenNeedSOS);
            NotificationHub.unRegister(tokenCancelSOS);
        };
    }, [])

    function onReceiveNeedSOS(message: ReceiveNeedSOSVehicleMessage) {
        setInSOS(true);
    }

    function onReceiveCancelSOS(message: ReceiveCancelSOSVehicleMessage) {
        if (message.vehicleSOS.length === 0) {
            setInSOS(false);
        }
    }

    const toggle = () => {
        setCollapsed(old => !old);
    };

    return (
        <Layout >
            <Sider
                collapsible
                trigger={null}
                collapsed={collapsed}
            >
                <Menu theme="dark" mode="inline" defaultSelectedKeys={['4']}>
                    <Menu.Item key="0" className="logo-brasao" disabled icon={<img src={policiaMilitarBrasao} className="side-menu-icon" alt="" />}>
                        <Link to="/map-2d">
                            Policia Militar
                        </Link>
                    </Menu.Item>
                    <Menu.Item key="2" icon={<VideoCameraOutlined />}>
                        <Link to="/map-2d">
                            Mapa 2d
                        </Link>
                    </Menu.Item>
                    <Menu.Item key="3" icon={<CarOutlined />}>
                        <Link to="/check-parts">
                            Frota
                        </Link>
                    </Menu.Item>
                </Menu>
            </Sider>
            <Layout className="site-layout" >
                <Header className="site-layout-background-header" style={{ background: inSOS ? "#ff4444" : undefined }}>
                    <BasicHeader collapsed={collapsed} toggle={toggle} />
                </Header>

                <Content className="container" >
                    <div className="site-layout-background" >
                        {props.children}
                    </div>
                </Content>
            </Layout>
        </Layout>
    );
}

export default BasicLayout
