import { Modal, Tabs } from 'antd'
import React from 'react'
import { useHistory } from 'react-router-dom';
import LastFillFormContainer from './components/LastFillFormContainer';

const { TabPane } = Tabs;

const HistoryCheckParts: React.FC = () => {

    const { push } = useHistory()

    return (
        <Modal
            visible
            bodyStyle={{ paddingTop: 0 }}
            cancelText="VOLTAR"
            width="70%"
            title="Historico dirio de bordo"
            onCancel={() => { push("/check-parts") }}
        >
            <Tabs defaultActiveKey="1" tabPosition="top" size="small">
                <TabPane tab="ltima ficha" key="1">
                    <LastFillFormContainer />
                </TabPane>
                <TabPane tab="Todas as fichas" key="2">
                    Todas as fichas
                </TabPane>
            </Tabs>
        </Modal>
    )
}

export default HistoryCheckParts
