import React, { useEffect, useState } from 'react'
import { Col, Row } from 'antd'
import { iconPoliceReport } from '../Icons/Icons'
import { Marker, Popup } from 'react-leaflet'
import { NotificationHub } from '../../../../../../hub/Notifications/NotificationHub'
import { PoliceReportMarkerProps } from './PoliceReportMarker.types'
import { ReceivePoliceReportMessage } from '../../../../../../hub/Notifications/models/ReceivePoliceReportMessage'
import FormatDateServices from '../../../../../../services/Utils/FormatDate/FormatDateServices'

import "./PoliceReportMarker.less";

const PoliceReportMarker: React.FC<PoliceReportMarkerProps> = (props) => {

    const [positions, setPositions] = useState<ReceivePoliceReportMessage[]>([])

    useEffect(() => {
        const keyLocation = NotificationHub.onReceivePoliceReport((message) => handlerReceivePoliceReport(message));

        return () => {
            NotificationHub.unRegister(keyLocation);
        }
    }, [])

    useEffect(() => {
        setPositions(props.policeReports);
    }, [props.policeReports])


    function handlerReceivePoliceReport(message: ReceivePoliceReportMessage) {
        setPositions(old => [...old, message]);
    }


    return (
        <>
            {positions?.map(position => (
                <Marker ref={e => e?.openPopup()}
                    icon={iconPoliceReport}
                    key={position.policeReportId}
                    position={{
                        lat: position.latitude,
                        lng: position.longitude
                    }}>
                    <Popup className="police-report-marker-popup">
                        <div className="police-report-marker-container">
                            <div className="police-report-marker-title">Ocorrncia</div>
                            <div className="police-report-marker-body">
                                <Row>
                                    <Col span={12}>
                                        <span className="police-report-marker-label">Data: </span>
                                        {FormatDateServices.formatDate(position.registedAt)}
                                    </Col>

                                    <Col span={12}>
                                        <span className="police-report-marker-label">Hora: </span>
                                        {FormatDateServices.formatTime(position.registedAt)}
                                    </Col>
                                </Row>

                                <div>
                                    <span className="police-report-marker-label">Solicitante: </span>
                                    {position.whistleblower ?? "Annimo"}
                                </div>

                                <div>
                                    <span className="police-report-marker-label">Detalhes: </span>
                                    {position.description}
                                </div>
                            </div>
                        </div>
                    </Popup>
                </Marker>
            ))}
        </>
    )
}

export default PoliceReportMarker
