import api from '../../../api/Api.configure';
import { ApiBase } from '../../../api/Api';
import { CreatePoliceReportCommand, CreatePoliceReportResult } from '../models/CreatePoliceReport';
import { RequestResponse } from '../../../models/RequestResponse';

export const endPoint: string = 'api/position-report';

class Api extends ApiBase {

    constructor() {
        super(endPoint);
    }


    async Save(command: CreatePoliceReportCommand): Promise<RequestResponse<CreatePoliceReportResult>> {
        return (await api.post(`${endPoint}/police-report`, command)).data
    }
}
export const PositionReportApi = new Api();