import { notification } from "antd";
import { ReceiveCancelSOSVehicleMessage } from "../../hub/Notifications/models/ReceiveCancelSOSVehicleMessage";
import { ReceiveNeedSOSVehicleMessage } from "../../hub/Notifications/models/ReceiveNeedSOSVehicleMessage";
import { NotificationHub } from "../../hub/Notifications/NotificationHub";

export class SynchronizeService {
    constructor() {
        this.configureService();
    }


    configureService() {
        let tokenNeedSOS = NotificationHub.onReceiveNeedSOS(this.onReceiveNeedSOS);
        let tokenCancelSOS = NotificationHub.onReceiveCancelSOS(this.onReceiveCancelSOS);
    }

    onReceiveNeedSOS(message: ReceiveNeedSOSVehicleMessage) {
        notification.error({
            duration: 100,
            className: "need-sos-notification-notice",
            message: "VIATURA NECESSITANDO AJUDA!",
            description: `Viatura ${message.model} placa ${message.licensePlate} solicitou SOS!`,
            key: message.vehicleId,
        })

    }

    onReceiveCancelSOS(message: ReceiveCancelSOSVehicleMessage) {

        notification.close(message.vehicleId)
    }

}

export default new SynchronizeService()