import * as Yup from "yup";
import { CreatePoliceReportCommand } from "../../models/CreatePoliceReport";
import { yupResolver } from '@hookform/resolvers/yup';

const CreatePoliceReportSchema: Yup.SchemaOf<CreatePoliceReportCommand> = Yup.object().shape({
    latitude: Yup.number().nullable().required(),
    longitude: Yup.number().nullable().required(),
    description: Yup.string().nullable().required("Ocorrncia  obrigatria.").min(3, "Ocorrncia  obrigatria."),
    isAnonymus: Yup.boolean().optional(),
    // whistleblower: Yup.string().required("Denunciante  obrigatria").nullable(),
    whistleblower: Yup.string().when("isAnonymus", {
        is: false,
        then: Yup.string().nullable().required("Denunciante  obrigatrio.").min(3, "Denunciante  obrigatrio."),
        otherwise: Yup.string().nullable().optional(),
    })
})



export default yupResolver<CreatePoliceReportCommand>(CreatePoliceReportSchema);