import { LatLng } from 'leaflet';
import React, { useCallback, useState } from 'react'
import { Marker, Popup, useMapEvents } from 'react-leaflet';
import { iconSelectedPosition } from '../Icons/Icons';
import PoliceReport from '../../../PoliceReport/PoliceReport';

import "./SelectedPosition.less"

const SelectedPosition: React.FC = () => {

    const [showPosiceReport, setShowPosiceReport] = useState<boolean>(false);
    const [selectedPosition, setSelectedPosition] = useState<LatLng>();

    useMapEvents({
        dblclick(e) {
            setSelectedPosition(e.latlng);
        }
    })

    const onCancel = useCallback(() => {
        setShowPosiceReport(false);
    }, [])

    const onSuccess = useCallback(() => {
        setShowPosiceReport(false);
        setSelectedPosition(undefined);

    }, [setShowPosiceReport, setSelectedPosition])


    if (!selectedPosition) {
        return null;
    }

    return (
        <>
            <Marker
                icon={iconSelectedPosition}
                key={selectedPosition.lat}
                position={selectedPosition}>
                <Popup className="selected-position-popup">
                    <div className="selected-position-container">
                        <div className="selected-position-title">Cadastro de Ponto</div>

                        <div className="selected-position-menu">
                            <div onClick={() => setShowPosiceReport(true)} className="selected-position-option">Cadastro de Ocorrncia</div>
                        </div>

                    </div>
                </Popup>
            </Marker>

            <PoliceReport isModalVisible={showPosiceReport} onCancel={onCancel} onSuccess={onSuccess} latitude={selectedPosition.lat} longitude={selectedPosition.lng} />
        </>
    )
}

export default SelectedPosition
